.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #e6e9e4;
  color: #127780;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date-month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date-year {
  font-size: 0.75rem;
}

.expense-date-day {
  font-size: 1.5rem;
  font-weight: bold;
}
